import {CALLRECORDLIST} from '../../../utils/api.js'
import {
	uniSearchBar,
	uniList,
	uniListItem
} from '../../../components/uni-icons/uni-icons.vue'
let that;
export default {
	components: {
		uniSearchBar,
		uniList,
		uniListItem
	},
	beforeCreate() {
		that = this
	},
	filters: {
		formatText:function(value){
			var mainText
			for(let i=0;i<value.length;i++){
				if(value[i].indexOf(that.key) >0){
					mainText = value[i]
				}
			}
			return mainText
		}
	},
	data() {
		return {
			key:"",
			callData:""
		}
	},
	onLoad(obj) {


	},
	methods: {
		// 搜索
		search(){
			const params = {
				searchKey: this.key
			}
			this.$request.url_request(CALLRECORDLIST,params,"GET",res =>{
				console.log(JSON.parse(res.data).data.list)
				this.callData = JSON.parse(res.data).data.list
			},err =>{})
		},
		// 通话详情
		getoDetail(id) {
			uni.navigateTo({
				url:'../callDetails/callDetails?id=' + id
			})
		},
		// 返回上一页
		goback(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
